* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: generalFont;
}

$bg_color:#333;
$border_color:#666;
$text_color:#fff;

.bg_movie {
    position: fixed;
    width: 100%;
    height: 100%;
    background: url(../img/movie-bg.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -11;
    overflow-y: auto;
}

.navbar {
    .container {
        .col-md-6 {
            a {
                font-size: 1.6rem;
                letter-spacing: .1rem;
            }
        }
    }
}


.card {
    position: relative;
    width: 100%;
    height: 100%;
    margin-bottom: 3rem;
    background-color: transparent;
    color: $text_color;
    z-index: 1;

    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #333;
        border-radius: .3rem;
        opacity: .7;
        z-index: -1;
    }
}

#movie-list-container {
    .card-body {
        width: 100%;
        height: 100%;

        #movie-list {
            li {
                margin-bottom: 1rem;
                transition: .2s ease-in-out;

                .flex-shrink-0 {
                    height: 444px;
                    background-color: $bg_color;
                    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
                    border: .01rem solid $border_color;
                    border-bottom: none;
                    border-radius: .3rem .3rem 0 0;

                    img {
                        border-radius: .3rem .3rem 0 0;
                    }
                }

                .flex-grow-1 {
                    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
                    height: 150px;
                    padding: 1rem;
                    border: .05rem solid $border_color;
                    background-color: $bg_color;
                    border-top: none;
                    border-radius: 0 0 .3rem .3rem;

                    .movie-tit {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        padding-bottom: 1.2rem;

                        h5 {
                            letter-spacing: .1rem;
                            font-size: 1.3rem;

                            a {
                                text-decoration: none;
                                color: $text_color;
                                transition: .2s ease-in-out;

                                &:hover {
                                    color: red;
                                }
                            }
                        }

                        span {
                            font-size: 1rem;
                            background-color: $border_color;
                            padding: .2rem .7rem;
                            border-radius: .3rem;
                        }

                        span.green {
                            color: lightgreen;
                        }

                        span.orange {
                            color: orange;
                        }

                        span.red {
                            color: red;
                        }
                    }
                }

                &:hover {
                    transform: scale(1.05);
                }
            }
        }
    }
}

#movie-details-container {
    .card-body {
        #movie-details {
            .col-xl-4 {
                img {
                    border: .01rem solid $border_color;
                    border-radius: .3rem;
                }
            }

            .col-xl-8 {
                span {
                    font-size: 1.2rem;
                    background-color: $border_color;
                    padding: .2rem .7rem;
                    border-radius: .3rem;
                }

                span.green {
                    color: lightgreen;
                }

                span.orange {
                    color: orange;
                }

                span.red {
                    color: red;
                }
            }
        }
    }
}

// Footer
#footer-section {
    position: relative;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: auto;
    background-color: $bg_color;
    color: $text_color;
    padding: 3rem 0;

    .container {
        display: flex;
        align-items: center;
        justify-content: center;

        .row {

            .socials,
            .copyR {
                display: flex;
                align-items: center;
                justify-content: center;
            }

            .socials {
                .socials-list {
                    display: flex;
                    align-items: center;
                    list-style: none;

                    .list-item {
                        margin: 0 .5rem;
                        font-size: 1.6rem;

                        a.instagram {
                            color: #ff3838;
                        }

                        a.github {
                            color: #171515;
                        }

                        a.facebook {
                            color: #4267B2;
                        }

                        a.mail {
                            color: #17c0eb;
                        }

                        a {
                            text-decoration: none;
                            transition: .4s ease;

                            &:hover {
                                color: #eee;
                            }
                        }
                    }
                }
            }

            .copyR {
                p {
                    a {
                        text-decoration: none;
                        color: #4267B2;
                    }
                }
            }
        }
    }
}